
/*************/
/* React APP */
/*************/

/*******design principles*******/
/*
menu breakpont 1024 px
interact area breakpoint 768 px
max screen breakpoint 1920 px

therefore the app will have 4 views
screen/components | menu    | interact area
upto 768px        | mobile  | mobile
upto 1024px       | mobile  | desktop
upto 1920px       | desktop | desktop
beyond 1920px  ---no further screen width expansion ---
*/

:root {
  /* App */
  --screen-width: 98.5vw;
  --screen-height: 100vh;
  --adjust-px: 5px;
  --app-min-height: calc(var(--screen-height) - var(--height-header) - var(--adjust-px));
  --app-width: calc(var(--screen-width) - var(--adjust-px));

  --interact-min-height: var(--app-min-height);
  --interact-width: var(--app-width);
  --interact-top: 0px;
  --interact-fixed-top: var(--height-header);

  /* Menu */
  --menu-button-height: 400px;
  --menu-button-width: 280px;
  --menu-button-color: var(--col-accent1-light);
  --menu-button-margin-lr: 20px;
  --menu-button-margin-tb: 20px;
  --menu-button-padding-lr: 10px;
  --menu-button-padding-tb: 10px;
  --menu-button-border-radius: 10px;
  --menu-button-shadow-xy: 5px;
  --menu-button-shadow-merge: 10px;
  --menu-button-shadow-start: -7px;
  --menu-button-shadow-color: var(--col-accent1-dd);
  --menu-button-hover-move-dist: 5px;

  --menu-button-contents-link-font-size: 20px;
  --menu-button-contents-design-font-size: 50px;
  --menu-button-contents-design-font-weight: 1000;
  --menu-button-contents-design-font-color: var(--white);

  /* Menu Icon */

  --menuicon-dim: 30px;
  --menuicon-top: calc(var(--height-header) + 15px);
  --menuicon-left: 15px;
  --menuicon-z-index: 2;

  --menuicon-dot-dim: 7px;
  --menuicon-dot-border-radius: 2px;
  --menuicon-dot-border-color: var(--col-accent2-dark);
  --menuicon-dot-margin: 1px;

  /*underconstruct*/
  --underconstruct-bg-color: var(--col-accent1);
  --underconstruct-dist: 50px;
  --underconstruct-font-size: 20px;
  --underconstruct-font-weight: 800;
  --underconstruct-border-radius: 10px;
  --underconstruct-alt-col1: var(--col-accent1);
  --underconstruct-alt-col2: var(--black);

  /*********************************/
  /********** Branding *************/
  /*********************************/

  /*BrandApp*/
  --brandapp-margin-tb: 0px;
  --brandapp-margin-lr: calc(var(--menuicon-dim) + (2 * var(--menuicon-left)));

  /*brandStepper*/
  --brandstepper-bg-color: var(--col-accent1-light);
  --brandstepper-wrapper-height: 50px;
  --brandstepper-wrapper-bg-color : var(--col-accent1-ll);
  --brandstepper-wrapper-div-margin-top : 100px;

  /*message before questionnaire*/
  --background-z-Index: 2;
  --background-color: var(--white);
  --background-opacity: 0.7;
  --message-wrapper-width: 280px;
  --message-wrapper-bg-color: var(--col-accent1-light);
  --message-wrapper-opacity: 1;
  --message-wrapper-border-radius: 10px;
  --message-padding: 30px;
  --message-margin-bottom: 30px;
  --close-x-button-padding-right: 5px;
  --close-big-button-padding-bottom: 40px;

  /*questionnaire*/
  --questionnaire-wrapper-grid-col: auto;
  --questionnaire-header-bg-color: var(--col-accent1-light);
  --questionnaire-header-border-radius: 5px;
  --questionnaire-header-height: calc(2 * var(--menuicon-dim));
  --questionnaire-slider-container-margin-tb: 25px;
  --questionnaire-slider-container-min-width: 150px;
  --questionnaire-message-padding-tb: 30px;
  --questionnaire-message-padding-lr: 0px;
  --questionnaire-logo-width : 40px;
  --questionnaire-logo-height : 40px;
  --questionnaire-logo-display: none;
  --questionnaire-logo-margin-tb: 0px;
  --questionnaire-logo-margin-lr: 50px;

  /*Brand results*/
  --result-wrapper-grid-template: auto;
  --palettes-grid-template: auto;

  --result-sec-color-font: 15px;
  --result-sec-space-font: 1.2px;
  --result-color-font-top-padding: calc(var(--palette-height) - (2 * var(--result-sec-color-font)));

  /*--palette-max-width: 500px;*/
  --palette-height: 100px;
  --palette-padding-tb: 0px;
  --palette-padding-lr: 0px;
  --palette-margin-tb: 20px;
  --palette-margin-lr: 0px;
  --palette-selected-border-width: 1px;
  --palette-selected-border-col: var(--col-header-border);

  /*Color Palette Details*/
  --palette-detail-position: fixed;

}
/* tablet */
@media all and (min-width: 768px) {
  :root {
    /*underconstruct*/
    --underconstruct-dist: 100px;
    --underconstruct-font-size: 50px;

    /*questionnaire*/
    --questionnaire-logo-display: unset;
  }
}

@media all and (min-width: 1024px) {
  :root {
    /*underconstruct*/
    --underconstruct-padding-tb: 30px;
    --underconstruct-marg-tb: 30px;
    --underconstruct-marg-lr: 40px;

    /*questionnaire*/
    --questionnaire-wrapper-grid-col: 15% 70% 17%;

    /*Brand results*/
    --result-wrapper-grid-template: 50% 50%;
    --palette-height: 100px;
    --palette-margin-tb: 20px;
    --palette-margin-lr: 40px;

    /*Color Palette Details*/
    --palette-detail-position: unset;
  }
}

@media all and (min-width: 1440px) {
  :root {
  }
}
@media all and (min-width: 1920px) {
  :root {
    --brandapp-margin-lr: calc(var(--menuicon-dim) + (2 * 15px));
    --menuicon-left: calc(((100vw - var(--width-webwrapper-max)) / 2) + 15px );
    --screen-width: var(--width-webwrapper-max);
  }
}
