#menuIcon1 {
  position: fixed;
  z-index: var(--menuicon-z-index);
  top: var(--menuicon-top);
  left: var(--menuicon-left);
  width: var(--menuicon-dim);
  height: var(--menuicon-dim);
  cursor: pointer;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-self: center;
}

.menuDot {
  width: var(--menuicon-dot-dim);
  height: var(--menuicon-dot-dim);
  border-radius: var(--menuicon-dot-border-radius);
  background-color: var(--menuicon-dot-border-color);
  margin: var(--menuicon-dot-margin);
}
