.menuitem {
  height: var(--menu-button-height);
  width: var(--menu-button-width);
  background-color: var(--menu-button-color);
  cursor: pointer;
  margin: var(--menu-button-margin-tb) var(--menu-button-margin-lr);
  padding: var(--menu-button-padding-tb) var(--menu-button-padding-lr);
  border-radius: var(--menu-button-border-radius);
  box-shadow: var(--menu-button-shadow-xy) var(--menu-button-shadow-xy) var(--menu-button-shadow-merge) var(--menu-button-shadow-start) var(--menu-button-shadow-color);
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: stretch;
}

.menuitem:hover {
  margin-top: calc(var(--menu-button-margin-tb) - var(--menu-button-hover-move-dist));
  margin-bottom: calc(var(--menu-button-margin-tb) + var(--menu-button-hover-move-dist));
  box-shadow: var(--menu-button-shadow-xy) var(--menu-button-shadow-xy) calc(2* var(--menu-button-shadow-merge)) var(--menu-button-shadow-start) var(--menu-button-shadow-color);
}


.menudesign1 {
  align-self: flex-start;
  writing-mode: vertical-lr;
}

.menudesign1 p {
  font-size: var(--menu-button-contents-design-font-size);
  font-weight: var(--menu-button-contents-design-font-weight);
  color: var(--menu-button-contents-design-font-color);
}

.menudesign2 {
  align-self: flex-end;
}

.menudesign2 p {
  font-size: var(--menu-button-contents-link-font-size);
  text-align: right;
}

.menudesign3 {
  align-self: flex-end;
}

.menudesign3 p {
  text-align: right;
  color: var(--menu-button-contents-design-font-color);
}

.menudesign4 {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
