#construct1 {
  width: var(--interact-width);
  min-height: var(--interact-min-height);
  top: var(--interact-top);

  display: flex;
  justify-content: center;
  align-items: center;

  background: linear-gradient(-45deg,
    var(--underconstruct-alt-col2) 0%, var(--underconstruct-alt-col2) 5%, var(--underconstruct-alt-col1) 5%, var(--underconstruct-alt-col1) 10%,
    var(--underconstruct-alt-col2) 10%, var(--underconstruct-alt-col2) 15%, var(--underconstruct-alt-col1) 15%, var(--underconstruct-alt-col1) 20%,
    var(--underconstruct-alt-col2) 20%, var(--underconstruct-alt-col2) 25%, var(--underconstruct-alt-col1) 25%, var(--underconstruct-alt-col1) 30%,
    var(--underconstruct-alt-col2) 30%, var(--underconstruct-alt-col2) 35%, var(--underconstruct-alt-col1) 35%, var(--underconstruct-alt-col1) 40%,
    var(--underconstruct-alt-col2) 40%, var(--underconstruct-alt-col2) 45%, var(--underconstruct-alt-col1) 45%, var(--underconstruct-alt-col1) 50%,
    var(--underconstruct-alt-col2) 50%, var(--underconstruct-alt-col2) 55%, var(--underconstruct-alt-col1) 55%, var(--underconstruct-alt-col1) 60%,
    var(--underconstruct-alt-col2) 60%, var(--underconstruct-alt-col2) 65%, var(--underconstruct-alt-col1) 65%, var(--underconstruct-alt-col1) 70%,
    var(--underconstruct-alt-col2) 70%, var(--underconstruct-alt-col2) 75%, var(--underconstruct-alt-col1) 75%, var(--underconstruct-alt-col1) 80%,
    var(--underconstruct-alt-col2) 80%, var(--underconstruct-alt-col2) 85%, var(--underconstruct-alt-col1) 85%, var(--underconstruct-alt-col1) 90%,
    var(--underconstruct-alt-col2) 90%, var(--underconstruct-alt-col2) 95%, var(--underconstruct-alt-col1) 95%, var(--underconstruct-alt-col1) 100%
  );
}

#construct2 {
  width: calc(var(--interact-width) - var(--underconstruct-dist));
  height: calc(var(--interact-min-height) - var(--underconstruct-dist));
  background-color: var(--underconstruct-bg-color);
  border-radius: var(--underconstruct-border-radius);

  display: flex;
  justify-content: center;
  align-items: center;

}

#construct2 p {
  color: var(--underconstruct-alt-col2);
  font-weight: var(--underconstruct-font-weight);
  font-size: var(--underconstruct-font-size);
}
